///* Doc Comment *///
.doc-comment-section {
  overflow-y: auto;
  padding-bottom: 10px;
  .comment-card-w-hint {
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto;
  }

  .comment-card {
    margin: 10px 10px 0;
    border: 1px solid #e8e8e8;
    border-radius: 4px;
    background-color: #fff;
    box-shadow: 0 1px 2px #e8e8e8;
  }

  .comment-field {
    position: relative;
    display: table;
    box-sizing: border-box;
    width: 100%;
    padding: 10px;
    table-layout: fixed;
    border-radius: inherit;
    background-color: #fff;
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto;

    .commenter-photo {
      display: table-cell;
      width: 32px;
      padding-right: 10px;
      vertical-align: top;
      white-space: nowrap;
    }
  }

  .mentions-container {
    position: relative;
    width: 100%;
    display: table-cell;
    vertical-align: top;

    .mentions-input {
      width: 100%;
      display: inline-block;
      min-height: 30px;
      word-wrap: break-word;
    }

    .comment-input-text {
      resize: none;
      overflow: hidden;
      width: 100%;
      background: rgb(252, 250, 216);
      border: none;
      padding: 5px;
      height: 30px;
    }
    .placeholder-text {
      box-sizing: border-box;
      position: absolute;
      padding: 6px;
      color: #aaa;
    }
  }

  .comment-box {
    display: block;
    width: 100%;
    vertical-align: top;

    .commenter-photo {
      -webkit-box-flex: 0;
      -webkit-flex: 0 0 auto;
      -ms-flex: 0 0 auto;
      flex: 0 0 auto;
      img {
        width: 32px;
        height: 32px;
      }
    }

    .mentions-container {
      -webkit-box-flex: 1;
      -webkit-flex: 1 1 auto;
      -ms-flex: 1 1 auto;
      flex: 1 1 auto;
    }

    .post-area {
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: end;
      -webkit-align-items: flex-end;
      -ms-flex-align: end;
      align-items: flex-end;
      -webkit-box-pack: justify;
      -webkit-justify-content: flex-end;
      -ms-flex-pack: justify;
      justify-content: flex-end;

      margin-top: 5px;
    }
  }

  .threaded-comment-list {
    padding-bottom: 10px;
    transition: box-shadow 0.2s ease, border 0.2s ease;
    color: #3d464d;
    .comment-activity {
      width: 100%;
      opacity: 1;
      .comment {
        display: table;
        box-sizing: border-box;
        width: 100%;
        padding: 10px 10px 0;
        table-layout: fixed;
        .commenter-photo {
          display: table-cell;
          width: 40px;
          padding-top: 4px;
          padding-right: 8px;
          vertical-align: top;
          white-space: nowrap;
          img {
            width: 32px;
            height: 32px;
          }
        }
        .comment-body {
          display: table-cell;
          width: 99%;
          vertical-align: top;
          .comment-top-bar {
            display: block;
            width: 100%;
            height: 20px;
            margin-bottom: 0;
            padding-top: 1px;
            .commenter-name {
              font-size: 15px;
              font-weight: 600;
              display: inline-block;
              overflow: hidden;
              max-width: 140px;
              margin-right: 5px;
              vertical-align: top;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            .comment-when {
              font-size: 11px;
              display: inline-block;
              color: #a1a1a1;
            }
          }

          .comment-text{
            font-size: 13px;
            line-height: 18px;
            white-space: pre-wrap;
            word-wrap: break-word;
            color: #4c545a;
            margin-top: 5px;
          }
        }
      }
    }

    .comment-reply-section {
      padding-left: 50px;
    }
  }

  .comment-field--reply {
    margin-top: 10px;
    padding-bottom: 0;
    border-top: 1px solid #e8e8e8;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    background-color: transparent;

    display: none;
  }
  .threaded-comment-list.focus-thread {
    .comment-field--reply {
      display: block;
    }
    .comment-reply-section {
      display: none;
    }
  }
}

.activity-list-section {
  .ca-feed-item {
    border-bottom: solid 1px #e8e8e8;
    padding: 10px;
    display: table;
    &:last-child {
      border-bottom: none;
    }

    .user-avatar {
      display: table-cell;
      margin-right: 10px;
      margin-bottom: 10px;
      img {
        width: 60px;
        height: 60px;
      }
    }
    .feed-body {
      display: table-cell;
      width: 100%;
      vertical-align: top;
    }

    .feed-user {
      margin-bottom: 10px;
    }
    .user-name {
      font-size: 1.25em;
      margin-top: 5px;
    }
    .comment-text {
      padding: 5px 10px;
      font-style: italic;
    }

    .activity-time {
      color: #888;
      font-size: 0.9em;
    }
  }
  .no-activity {
    text-align: center;
    padding: 30px 0px;
  }
}

.user-avatar-settings-wrapper {
  display: table;
  .user-avatar-image {
    display: table-cell;
    padding: 10px 15px;
  }
  .user-avatar-update {
    display: table-cell;
  }
}

.video-list {
  .mark-watched-bg {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 60px 60px 0 0;
    border-color: #a3a3a3 transparent transparent transparent;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 1000;
    opacity: 0.8;
  }
  .mark-watched {
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 1001;
    font-size: 24px;
    top: 3px;
    left: 5px;
    color: #0A4390;
  }

  .video-info {
    color: #888;
    .video-title {
      font-size: 1.1em;
      color: #48B0F7;
      font-weight: 700;
    }
  }

  .video-price {
    color: red;
  }
  .video-thumbnail {
    position: relative;
    .video-duration {
      position: absolute;
      right: 5px;
      bottom: 5px;
      padding: 0px 5px;
      color: white;
      background: #000;
      opacity: 0.7;
      font-weight: bold;
    }
    .video-price {
      text-align: center;
      background-color: rgba(200, 200, 200, 0.5);
      padding: 5px 10px;
      color: red;
      font-size: 30px;
      font-weight: 700;
      position: absolute;
      left: 10px;
      top: 10px;
    }
  }
}

.main-header .notifications-menu {
  .user-noti-list > ul.menu {
    max-height: 500px;
    > li > a {
      overflow:initial;
      text-overflow: unset;
      white-space: normal;
      word-wrap: break-word;
    }
  }
}

.notifications-menu .dropdown-menu {
  z-index: 99999;
}

.notification-list {
  .noti-item {
    display: table;
    width: 100%;
    padding: 7px 10px;
    margin: 1px 0px;
    background: @gray-lighter;
    .noti-remove-icon {
      display: table-cell;
      vertical-align: middle;
    }
    a.noti-remove {
      font-size: 16px;
      font-weight: normal;
      color: @gray-light;
      display: block;
      margin-right: 10px;
    }
    .noti-message {
      display: table-cell;
      vertical-align: middle;
      width: 100%;
    }
    .noti-time {
      display: table-cell;
      vertical-align: middle;
      text-align: right;
      min-width: 60px;
      color: @gray-light;
    }
  }
}



.claim-billing-doc-section {
  .billing-reply-doc {
    td:first-child {
      padding-left: 30px;
    }
  }
}

#user-picture-modal {
  .upload-msg {
    text-align: center;
    padding: 50px;
    font-size: 22px;
    color: #aaa;
    width: 260px;
    margin: 50px auto;
    border: 1px solid #aaa;
  }

  .upload-picture-wrap {
    display: none;
  }

  &.ready {
    .upload-msg {
      display: none;
    }
    .upload-picture-wrap {
      display: block;
    }
  }

  .croppie-container {
    padding: 30px 0px;
  }
}



/* smaller than Extra small screen */
@media (max-width: (@screen-sm-max)) {
  ul.claim-photo-section li {
    width: 25%;
  }
}


/* smaller than Extra small screen */
@media (max-width: (@screen-xs-max)) {
  .navbar-nav .notifications-menu.open .dropdown-menu {
    background: #fff;
    position: absolute;
  }

  .user-nav.navbar-nav {
    color: white;
  }
  .navbar-default .user-nav.navbar-nav .open .dropdown-menu > li > a {
    color: white;
    &:hover, &:focus {
      color: #ccc;
    }
  }

  ul.claim-photo-section li {
    width: 33%;
  }
}

/* smaller than 640px screen */
@media (max-width: (639px)) {
  ul.claim-photo-section li {
    width: 50%;
  }
}

/* smaller than 480px screen */
@media (max-width: (479px)) {
  ul.claim-photo-section li {
    width: 100%;
  }
}
